// Font faces
//
// Instead of relying on the fonts that are available on a user's computer, you
// can use web fonts which, like images, are resources downloaded to the user's
// browser. Because of the bandwidth and rendering resources required, web fonts
// should be used with care.
//
// Numerous resources for web fonts can be found on Google. Here are a few
// websites where you can find Open Source fonts to download:
// - http://www.fontsquirrel.com/fontface
// - http://www.theleagueofmoveabletype.com
//
// In order to use these fonts, you will need to convert them into formats
// suitable for web fonts. We recommend the free-to-use Font Squirrel's
// Font-Face Generator:
//   http://www.fontsquirrel.com/fontface/generator
//
// The following is an example @font-face declaration. This font can then be
// used in any ruleset using a property like this:  font-family: Example, serif;
//
// Since we're using Sass, you'll need to declare your font faces here, then you
// can add them to the font variables in the _init.scss partial.

@font-face {
    font-family: 'roboto_condensedregular';
    src: url('../fonts/Roboto/robotocondensed-regular-webfont.woff2') format('woff2'),
         url('../fonts/Roboto/robotocondensed-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@import url(//fonts.googleapis.com/css?family=Open+Sans:400,700,300,600,800);
@import url(//fonts.googleapis.com/css?family=Gentium+Basic:400,700);

$bodybg: #FFF;
$headerfont: 'Open Sans', sans-serif;
$altheaderfont: 'Gentium Basic', serif;


// Icons ADC
$icomoon-font-path: '../fonts/Icons' !default;


@font-face {
  font-family: 'icoadc';
  src:  url('#{$icomoon-font-path}/icoadc.eot?1xqduc');
  src:  url('#{$icomoon-font-path}/icoadc.eot?1xqduc#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/icoadc.ttf?1xqduc') format('truetype'),
    url('#{$icomoon-font-path}/icoadc.woff?1xqduc') format('woff'),
    url('#{$icomoon-font-path}/icoadc.svg?1xqduc#icoadc') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^='icon-'], [class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icoadc' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

